	module.exports = {"data":{"markdownRemark":{"html":"<p>悪夢だ……家に着いたら鍵がないことが発覚。今日立川行ったとき寄った漫画喫茶に置き去りにしてしまった可能性が高い。立川まで終電で間に合いそうだけどそのまま現地泊まり確定orz 交通費とかｱｲﾀﾀﾀ 自分の不注意さ加減にｱｲﾀﾀﾀ</p>\n<h2>12/2 2:20追記</h2>\n<p>立川の漫画喫茶にて無事鍵を発見したは良かったが、思ったより夜遅くまで電車のダイヤがあったので、もしかして帰れるんじゃね？と思ったが運の尽き。</p>\n<ul>\n<li>武蔵野線に乗ったら東所沢で終了</li>\n<li>東京の直ぐ隣→都会だろと舐めてかかってたがなんもねぇ</li>\n<li>漫画喫茶を探して清瀬まで延々と歩く</li>\n<li>漫画喫茶が24時間営業じゃないorz</li>\n<li>仕方なく駅前のシダックスに避難\nまぁいいさここ1年近くカラオケなんて行ってなかったし、丁度練習になるわな( 'Ａ')＝３</li>\n</ul>","frontmatter":{"cover":null,"date":"2005-12-01T22:11:00+09:00","strDate":"2005/12/1","title":"あにゃああああ"}}},"pathContext":{"path":"/ja/blog/2005/12/01-あにゃああああ/","langKey":"ja"}}

